import React, { memo, useState, Fragment } from 'react'
import useFetch from 'use-http'
import {isEmpty, range, find, get, toString} from 'lodash-es'
import {ProgressBar} from 'react-bootstrap'

function getLoaihinhText(props) {
    return `${props.nhom} - ${props.loaihinh}`
}

function monthField(data, month, field) {
    console.log(data, toString(month), find(data, {month: toString(month)}))
    return get(find(data, {month: toString(month)}), field, 0)
}

function Pop(props = {}) {
    const [giamsats, setGiamsats] = useState([])
    const { get, response, loading, error } = useFetch('/pt_nguyco')

    let link = '/pt_nguyco/default/update?id=' + props.gid

    async function getGiamsats() {
        const newGss = await get(`/thongke/giamsat?id=${props.gid}`)
        if (response.ok) setGiamsats([...giamsats, newGss])
    }

    return (
        <div>
            <div className="pop-wrapper">
                <div className="pop-title text-uppercase">
                    <a href={link} target="_blank">
                        <div> {props.ten_cs}</div>
                    </a>
                </div>
                <div className="pop-body" style={{maxHeight: 350}}>
                    <ul className="list-data mb-10 mb-2">
                        <li><b>Tn/ Ngi:</b> <span>{props.ten_cs}</span></li>
                        <li><b>a ch:</b> <span dangerouslySetInnerHTML={{__html: props.diachi}} /> - {props.tenquan} - {props.tenphuong}</li>
                        <li><b>Khu ph - T dn ph:</b> <span>{props.khupho_to}</span></li>
                        <li><b>Nhm - Loi hnh:</b> <span>{getLoaihinhText(props)}</span></li>
                        <li><b>Ngy cp nht:</b> <span>{props.ngaycapnhat}</span></li>
                    </ul>
                    {loading ? <ProgressBar animated now={100} /> : (!isEmpty(giamsats) && response && response.data) && (
                        <Fragment>
                            <hr/>
                            <div className="table-responsive" style={{maxHeight: 160}}>
                                <table className="table table-sm">
                                    <thead>
                                    <tr>
                                        <th>Thng</th>
                                        <th>S ln GS</th>
                                        <th>S ln c LQ</th>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    {range(1, 13).map((m, k) => (
                                        <tr key={k}><td>{m}</td><td>{monthField(response.data.data, m, 'count_gs')}</td><td>{monthField(response.data.data, m, 'count_lq')}</td></tr>
                                    ))}
                                    </tbody>

                                </table>
                            </div>
                        </Fragment>
                    )}

                    <ul className="list-actions list-inline mb-0 mt-2">
                        <li className="list-inline-item"><a href={link} target="_blank">Chi tit</a></li>
                        <li className="list-inline-item"><a onClick={getGiamsats}>Lch s gim st</a></li>
                    </ul>
                </div>
            </div>

        </div>
    )
}

export default memo(Pop)